header {
  height: em(80);
  border-bottom: em(1) solid $emerald;
  position: relative;
  background-color: #fff;
  z-index: 99999;
  border-top: em(3) solid $emerald;
}

#mobile-nav {
  position: absolute;
  z-index: 500;
  width: calc(100% - 0.33em);
  top: em(-325);
  transition: top 400ms ease-in-out;
  ul {
    border-left: em(2) solid #fff;
    border-right: em(2) solid #fff;
  }
  li {
    border-bottom: em(1) solid $rTitle;
    background-color: $rTitle;
    padding-left: em(15);
    padding-right: em(15);
    &:first-child {
      padding-top: em(15);
    }
    &:last-child {
      border-bottom: em(2) solid #fff;
      padding-bottom: em(15);
    }
    a {
      z-index: 505;
      cursor: pointer;
      padding: em(10) 0;
      text-align: center;
      display: block;
      color: #fff;
      &:focus, &:hover {
        background-color: $rTitleHover;
      }

    }
  }
  @media only screen and (min-width: em(1024)) {
    display: none;
  }
}

.slide-down {
  top: em(80) !important;
}