
* {
  @include box-sizing(border-box);

  // TODO: verwijder dit, enkel gebruikt om te developen
  //background-color: rgba(0,0,0,.1);
}

.clearfix:after {
  content: ' '; // Older browser do not support empty content
  visibility: hidden;
  display: block;
  height: 0;
  clear: both;
}

.shadow-green {
  box-shadow: 0 em(4) em(5) 0 rgba($emerald, 0.20),0 em(1) em(10) 0 rgba($emerald, 0.06),0 em(2) em(4) em(-1) rgba($emerald, 0.15);
}

.shadow-normal {
  box-shadow: 0 em(4) em(5) 0 rgba(0,0,0,0.11),0 em(1) em(10) 0 rgba(0,0,0,0.09),0 em(2) em(4) em(-1) rgba(0,0,0,0.15);
}

.greyDivider {
  background-color: $rGray;
  height: em(30);
  @media only screen and (min-width: em(1024)) {
    height: em(55);
  }
}

#goTop {
  background-color: rgba($rTitle, 0.3);
  border-radius: 100%;
  position: fixed;
  z-index: 999998;
  padding: em(5) em(5);
  color: #fff;
  bottom: em(75);
  right: em(20);
  display: none;
  transition: background-color 150ms ease-in-out;
  &:hover {
    background-color: rgba($rTitle, 0.8);
  }
  img {
    vertical-align: bottom;
  }
}

.greenLink {
  color: $emerald;
  text-decoration: underline;
  &:hover {
    color: $grass;
  }
}