.carouselStory {
  position: absolute;
  color: #fff;
  background-color: rgba(135, 166, 42, 0.9);
  min-width: em(200);
  width: 8%;
  text-align: left;
  transform: translate(-50%, -50%);
  top: 45%;
  padding: 0.625em;
  transition: box-shadow 200ms ease-in-out;
  &:hover {
    box-shadow: 0 em(8) em(10) em(1) rgba(0, 0, 0, 0.14), 0 em(3) em(14) em(2) rgba(0, 0, 0, 0.12), 0 em(5) em(5) em(-3) rgba(0, 0, 0, 0.3);
  }

  &:hover > .readStoryBtn {
    background-color: $emerald;
  }

  @media only screen and (min-width: em(600)) {
    max-width: 25em;
    width: auto;
  }

  @media only screen and (min-width: em(1000)) {
    top: inherit;
    bottom: -10%;
  }
}

.readStoryBtn {
  color: #FFF;
  vertical-align: bottom;
  display: inline-block;
  padding: 0.625em;
  background-color: #282a37;
  float: right;
  transition: background-color 200ms ease-in-out;
}

#story1 {
  left: 16.666666%;
  @media only screen and (min-width: em(1000)) {
    left: inherit;
    right: 63.5%;
  }
  @media only screen and (min-width: em(1800)) {
    left: inherit;
    right: 64.5%;
  }
  @media only screen and (min-width: em(2500)) {
    left: inherit;
    right: 65%;
  }
}

#story2 {
  left: 49.999998%;
  @media only screen and (min-width: em(1000)) {
    left: inherit;
    right: 31%;
    bottom: -7%;
  }
  @media only screen and (min-width: em(1800)) {
    right: 31.75%;
    bottom: -8%;
  }
  @media only screen and (min-width: em(2500)) {
    right: 32.1%;
  }
}

#story3 {
  left: 83.33333%;
  @media only screen and (min-width: em(1000)) {
    left: inherit;
    right: -3.15%;
    bottom: -10%;
  }
  @media only screen and (min-width: em(1800)) {
    right: -2.15%
  }
  @media only screen and (min-width: em(2500)) {
    right: -1.65%;
  }
}

