#imgIndicator {
  position: absolute;
  text-align: center;
  width: 100%;
  height: em(50);
  bottom: 0.1875em;
  li {
    display: inline-block;
    position: relative;
    height: em(16);
    width: em(20);
    margin: em(17) em(4);
    background-color: rgba(255, 255, 255, 0.5);
    transition: background-color .4s;
    border: em(1) solid #fff;
