}

.navigateCarousel {
  display: inline-block;
  position: absolute;
  z-index: 5;
  padding: em(5) em(5.09);
  cursor: pointer;
  vertical-align: top;
  top: 51%;
  height: em(53);
  img {
    position: relative;
  }
  &:active > img {
    transform: translateY(-10%);
  }
}

#leftArrow {
  transform: rotate(-90deg);
  float: left;
  left: 0;
}

#rightArrow {
  transform: rotate(90deg);
  float: right;
  right: 0;
}

