a {
  color: rgba(255, 255, 255, 0.6);
}

#lampadario {
  position: fixed;
  left: 50%;
  top: 0;
}

#filo {
  position: relative;
  background-color: #000;
  width: em(2);
  height: em(100);
  left: 50%;
  margin-left: em(-1);
  z-index: 1;
  transform-origin: 0 0;
  //animation: oscillaFilo .9s ease-in-out 0s infinite alternate;
}

#filo:after {
  content: " ";
  left: em(-5);
  top: 100%;
  position: absolute;
  border-bottom: em(15) solid $rTitle;
  border-left: em(4) solid transparent;
  border-right: em(4) solid transparent;
  height: 0;
  width: em(4);
}

#lampadina {
  position: relative;
}

input[value="off"]:checked ~
#filo {
  box-shadow: em(-80) em(0) em(7) 0 rgba(0, 0, 0, .2);
}

input[value="off"]:checked ~
#filo:after {
  box-shadow: em(-80) em(-10) em(-10) em(-2) rgba(0, 0, 0, .1);
}

input {
  position: absolute;
  width: em(90);
  height: em(70);
  top: em(150);
  margin-left: em(-45);
  visibility: hidden;
  z-index: 1;
  cursor: pointer;
}

input[value="on"] {
  top: 115%;
}

input[value="off"] {
  top: 0;
}

input[value="on"]:checked {
  top: 0;
}

input[value="on"]:checked + input[value="off"] {
  top: 115%;
}

label {
  width: em(51);
  height: em(51);
  top: em(114);
  position: absolute;
  left: 0;
  margin-left: em(-26);
  border-radius: 100%;
  //animation: oscillaLampadina .9s ease-in-out 0s infinite alternate;
}

input[value="off"]:checked ~
label {
  background: rgba(255, 255, 255, .03);
  box-shadow: inset 0 em(1) em(5) rgba(255, 255, 255, .6), inset 0 em(2) em(20) rgba(255, 255, 255, 0.07), em(-81) em(-20) em(15) em(-5) rgba(0, 0, 0, .1);
}

input[value="on"]:checked ~
label {
  background: rgba(255, 255, 255, 1);
  box-shadow: 0 0 em(10) rgba(255, 255, 255, 0.8), 0 0 em(30) rgba(255, 255, 255, 0.8), 0 0 em(50) rgba(255, 255, 255, 0.6), 0 0 em(70) rgba(255, 255, 255, 0.6), em(-80) em(-15) em(120) 0 rgba(255, 255, 255, .4);
}

input[value="off"]:checked ~
label:after {
  content: " ";
  width: em(15);
  height: em(15);
  top: 0;
  position: absolute;
  left: 0;
  margin-left: em(15);
  border-radius: 100%;
  border: em(2) solid rgba(255, 255, 255, .03);
}

#sorpresa {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

input[value="off"]:checked ~
#lampadina #sorpresa {
  opacity: 0;
}

input[value="on"]:checked ~
#lampadina #sorpresa {
  opacity: 1;
}

#footer {
  position: fixed; // if used check absolute for IE
  width: 94%;
  text-align: center;
  bottom: 30%;
  padding: 0 3%;
  z-index: 1;
}

#shadow {
  position: fixed; // if used check absolute for IE
  width: 94%;
  text-align: center;
  bottom: 29%;
  padding: 0 3%;
  color: rgba(0, 0, 0, 0);
  text-shadow: 0 0 em(4) rgba(0, 0, 0, .3);
  transform-origin: 50% 0;
  //animation: ombraTesto .9s ease-in-out 0s infinite alternate;
}

@keyframes oscillaFilo {
  from {
    transform: rotate(7deg);
  }
  to {
    transform: rotate(-7deg);
  }
}

@keyframes oscillaLampadina {
  from {
    transform: rotate(3deg) translate(em(-16.4), em(-1));
  }
  to {
    transform: rotate(-3deg) translate(em(16.4), em(-1));
  }
}

@keyframes ombraTesto {
  from {
    transform: translate(em(1), 0) scale(1.01, 1.06) skew(0.9deg, 0deg);
  }
  to {
    transform: translate(em(-1), 0) scale(1.01, 1.06) skew(-0.9deg, 0deg);
  }
}