nav {
  height: 100%;
  position: relative;
  z-index: 99;
  background-color: #fff;

  @media only screen and (min-width: em(1021)) {
    max-width: 70%;
    min-width: em(887);
    margin: 0 auto;
    #hamburgerContainer {
      display: none;
    }
  }
}

#brand {
  height: em(70);
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

#hamburgerContainer {
  display: inline-block;
  cursor: pointer;
  height: 80%;
  position: absolute;
  top: 10%;
  padding: 0 em(20);
  right: 0;
  margin-right: em(7);
  &:hover > .hamburger, &:hover > .hamburger:before {
    border-color: lighten($emerald, 12%);
  }
}

.hamburger {
  position: relative;
  display: inline-block;
  top: 50%;
  transform: translateY(-50%);
  width: em(35);
  height: em(22);
  border-top: em(3) solid $emerald;
  border-bottom: em(3) solid $emerald;
}

.hamburger:before {
  content: "";
  position: absolute;
  top: em(7);
  left: 0;
  width: em(35);
  border-top: em(3) solid $emerald;
}

#navLinkContainer {
  display: none;
  @media only screen and (min-width: em(1021)) {
    display: block;
    float: right;
    height: 100%;
    .navLink {
      cursor: pointer;
      display: inline-block;
      height: 100%;
      padding: em(12);
      span {
        display: inline-block;
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        color: $rTitle;
        font-weight: 400;
        font-size: em(18);
      }
      &:hover {
        background-color: $emerald;
        > span {
          color: #fff;
        }
      }
    }
  }
}

